/* ONLY FOR IE11 */
@media all and (-ms-high-contrast:none) {
	body {
		overflow: hidden;
	}
	
	.header {
		height: 970px;
	}
	.header__center {
		margin-top: 200px;
	}
	
	.header__end {
		display: none;
	}
}

/* ONLY FOR EDGE */
@supports (-ms-ime-align:auto) {
	body {
		overflow: hidden;
	}
}