.slider {
	user-select: text;
}

.b-lazy {
	transition: opacity .5s linear;
	max-width: 100%;
	opacity: 0;
}

.b-lazy.b-loaded {
	opacity: 1;
}


// titles for semantics and structure HTML
.offscreen {
	position: absolute;
	clip: rect(1px 1px 1px 1px); /* for Internet Explorer */
	clip: rect(1px, 1px, 1px, 1px);
	padding: 0;
	border: 0;
	height: 1px;
	width: 1px;
	overflow: hidden; 
}

.btn {
	padding: 12px 50px;
	font-size: 18px;
	font-weight: 500;
	transition: all .3s linear;
}
.btn-white {
	color: #162f40;
	background: #fff;
	&:hover{
		background: darken(#fff, 15%);
	}
}
.btn-black {
	position: relative;
	overflow: hidden;
	display: block;
	padding: 12px 0;
	margin: 0 auto 30px auto;
	width: 230px;
	color: #fff;
	background: #000;

	&:after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: -4.5em;
		height: 100%;
		width: 3em;
		transform: skewX(-45deg) translateX(0);
		background-color: rgba(255,255,255,0.5);
	}

	&:hover {
		&:after {
			transform: skewX(-45deg) translateX(25em);
			transition: all .5s linear;
		}
	}
}

.price.ru  {
	display: none;
}